import juice from 'juice'
import { admin } from '~/mixins/admin_pages.js'
import ckeditor5const from '~/mixins/ckeditor5const'
import { validationMixin } from 'vuelidate'
import { required } from 'vuelidate/lib/validators'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

var timezone = process.env.TIMEZONE
const dev = process.env.DEV_API
const prod = process.env.PROD_API
const url = process.env.NODE_ENV === 'development' ? dev : prod

var timezone = process.env.TIMEZONE
export default {
  mixins: [validationMixin, admin],

  head: () => ({
    title: 'Post Datatable'
  }),

  data: () => ({
    form_tags: [],
    items: [],
    headers: [
      {
        text: 'No',
        align: 'start',
        sortable: false,
        value: 'no'
      },
      { text: 'Name', value: 'name' },

      { text: 'Title', value: 'title' },
      { text: 'Slug', value: 'slug' },
      { text: 'Publish', value: 'publish' },
      { text: 'Date / Time', value: 'created_at' },
      { text: 'Action', value: 'id', sortable: false }
    ],
    form_content: '',
    form_title: '',
    form_publish: '',
    dialog: false,
    dialogDelete: false,
    deletedialog: false,
    editedIndex: -1,
    search: '',
    tabledata: [],
    tabledata_total: 0,
    loading: true,
    options: {},

    publishselection: [
      {
        value: 1,
        text: 'Draft'
      },
      {
        value: 2,
        text: 'Publish'
      }
    ],
    form_image: '',

    image: '',
    image_preview: '',
    image_name: ''
  }),
  validations: {
    form_content: { required },
    form_title: { required },
    form_publish: { required }
  },
  async created() {
    this.timezone = timezone
    this.editorConfig = {
      simpleUpload: {
        uploadUrl: url + '/' + 'api/ckeditor',
        withCredentials: true,
        headers: {
          Accept: 'application/json',
          Timezone: this.timezone,
          identifier: this.image_id,
          'X-XSRF-TOKEN': this.$auth.$storage.getCookies()['XSRF-TOKEN']
        }
      }
    }
  },
  components: {
    'ckeditor-nuxt': () => import('@coderzero8/ck5-nuxt')
  },
  computed: {
    titleErrors() {
      const errors = []
      if (!this.$v.form_title.$dirty) return errors
      !this.$v.form_title.required && errors.push('Title is required.')
      return errors
    },
    contentErrors() {
      const errors = []
      if (!this.$v.form_content.$dirty) return errors
      !this.$v.form_content.required && errors.push('Content is required.')
      return errors
    }
  },
  watch: {
    options: {
      handler() {
        this.getDataFromApi()
      },
      deep: true
    },
    dialog(val) {
      val || this.close()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    }
  },
  async fetch() {
    await this.$axios.$get('/sanctum/csrf-cookie')
    let response = await this.$axios.$get('admin/blog/tags/data')
    for (const [key, value] of Object.entries(response.data)) {
      this.items = [...this.items, value.name]
    }
  },
  mounted() {
    this.getDataFromApi()
  },
  methods: {
    handleFileUpload(e) {
      const file = this.$refs.file.files[0]
      this.image_preview = URL.createObjectURL(file)
      try {
        this.image_name = this.$refs.file.files[0].name
        this.form_image = this.$refs.file.files[0]
      } catch (err) {
        console.log(err)
      }
    },
    remove_image() {
      this.$refs.file.value = null
      this.image_name = ''
      this.image = ''
      this.image_preview = ''
      this.form_image = ''
      return false
    },
    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete() {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },
    editItem(item) {
      this.image_id = this.tabledata[this.tabledata.indexOf(item)].ckeditor_log
      this.form_title = this.tabledata[this.tabledata.indexOf(item)].title
      this.form_image = this.tabledata[this.tabledata.indexOf(item)].image
      this.form_content = this.tabledata[this.tabledata.indexOf(item)].content
      this.form_tags = this.tabledata[this.tabledata.indexOf(item)].tags
      this.form_publish = this.tabledata[
        this.tabledata.indexOf(item)
      ].publishvalue
      this.editedIndex = this.tabledata.indexOf(item)
      this.dialog = true
    },

    deleteItem(item) {
      this.editedIndex = this.tabledata.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },
    async SaveEdited() {
      await this.$axios.$get('/sanctum/csrf-cookie').then(response => {})
      NProgress.start()
      let payload = new FormData()
      let table_id = this.tabledata[this.editedIndex].id

      this.form_content = juice.inlineContent(
        this.form_content,
        ckeditor5const.styles
      )

      payload.append('tags', this.form_tags)
      payload.append('post_id', this.tabledata[this.editedIndex].id)
      payload.append('title', this.form_title)
      payload.append('content', this.form_content)
      payload.append('publish', this.form_publish)
      payload.append('image', this.form_image)

      try {
        this.$axios
          .$post(`admin/blog/update/${table_id}`, payload, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          })
          .then(res => {
            this.tabledata[this.editedIndex].tags = this.form_tags
            this.tabledata[this.editedIndex].title = this.form_title
            this.tabledata[this.editedIndex].content = this.form_content
            this.tabledata[this.editedIndex].publish =
              this.form_publish == 1 ? 'Draft' : 'Publish'

            this.tabledata[this.editedIndex].publishvalue = this.form_publish

            // if (this.form_image) {
            this.tabledata[this.editedIndex].image = res.image
            // }

            this.dialog = false
            this.form_publish = ''
            this.image_preview = ''
            this.$fetch()
            NProgress.done()
          })
          .catch(error => {
            this.form_publish = ''
            NProgress.done()
          })
          .finally(() => {})
      } catch (error) {}
    },
    async deleteItemConfirm() {
      await this.$axios.$get('/sanctum/csrf-cookie').then(response => {})
      let table_id = this.tabledata[this.editedIndex].id
      try {
        this.$axios
          .$delete(`admin/blog/delete/${table_id}`)
          .then(res => {})
          .catch(error => {})
          .finally(() => {})
      } catch (error) {}
      this.tabledata.splice(this.editedIndex, 1)
      this.closeDelete()
    },
    async getDataFromApi() {
      this.loading = true
      const { sortBy, sortDesc, page, itemsPerPage } = this.options

      let payload = new FormData()
      payload.append('sortDesc', sortDesc)
      payload.append('sortBy', sortBy)
      payload.append('page', page)
      payload.append('itemsPerPage', itemsPerPage)
      payload.append('search', this.search)

      await this.$axios.$get('/sanctum/csrf-cookie').then(response => {})
      this.$axios
        .$post('admin/blog/datatable', payload)
        .then(res => {
          var data = []
          var rowcount = 1
          if (page == 1) {
            rowcount = 1
          } else {
            rowcount = (page - 1) * itemsPerPage + 1
          }

          for (const [key, value] of Object.entries(res.data)) {
            data.push({
              no: rowcount,
              name: value.name,
              id: value.id,
              slug: value.slug,
              title: value.title,
              content: value.content,
              publish: value.publish == 1 ? 'Draft' : 'Publish',
              publishvalue: value.publish,
              image: value.image,
              created_at: value.human_date,
              ckeditor_log: value.ckeditor_log,
              tags: value.tags
            })
            rowcount++
          }

          this.tabledata = data
          this.tabledata_total = res.total
          this.loading = false
        })
        .catch(error => {
          this.loading = false
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
